import React from "react";
import ResultImg from '../components/resultImg';
import Footer from '../components/footer';
import { useLocation, Link } from "react-router-dom";
import { getCharCountFromHash } from "../util/getCharCountFromHash";
import Data from "../data/result.json";

const Result = () => {
  const location = useLocation();
  const name = location.state?.name
  const shaName = location.state?.sha256
  const DataLength = Data.result.length
  const index = getCharCountFromHash(shaName) % DataLength
  return (
    <div>
      <div className="result">
        <ResultImg number={index}/>
        <h1>{name} </h1>
        <h2>{Data.result[index].title}</h2>
        <h3>{Data.result[index].description}</h3>
        <p>
          <a href={makeTwitterShareURL(Data.result[index].title)} target="_blank">""</a>
        </p>
        <Link to="/" className="end">
            
        </Link>
      </div>
      <Footer/>
    </div>
  );
}
export default Result;

const makeTwitterShareURL = (result) => {
  const url = "https://atria64.github.io/ria-mikuji/";
  const text = " \"" + result + "\" %0a\"\""
  const viaUser = "AtriaSoft"
  const hashtags = ""
  return "https://twitter.com/share?url=" + url
  + "&text=" +text
  + "&via=" +viaUser
  + "&hashtags=" +hashtags
}