const getCharCountFromHash = (shaText) => {
    const aCount = shaText.match(/a/g)?.length ?? 0;
    const bCount = shaText.match(/b/g)?.length ?? 0;
    const cCount = shaText.match(/c/g)?.length ?? 0;
    const dCount = shaText.match(/d/g)?.length ?? 0;
    const eCount = shaText.match(/e/g)?.length ?? 0;
    const fCount = shaText.match(/f/g)?.length ?? 0;
    return aCount+bCount+cCount+dCount+eCount+fCount;
}
module.exports = {
    getCharCountFromHash,
};