import React, { useState } from "react";
import ria from '../img/ria.svg';
import Footer from '../components/footer';
import { Link } from "react-router-dom";

const NameSet = () => {

  const [text,setText] = useState("");

  return (
    <div>
      <div className="top">
        <img src={ria}></img>
        <h1></h1>
        <input
          type="text"
          placeholder=""
          value={text}
          onChange={(event)=>setText(event.target.value)}/>
        <p>
          <Link to="/loading" state={{name: text}}>
            
          </Link>
        </p>
        <p>
          <Link to="/" className="end">
            
          </Link>
        </p>
      </div>
      <Footer/>
    </div>
  );
}
export default NameSet;