import React from "react";
import ria from '../img/ria.svg';
import Footer from '../components/footer';
import { Link } from "react-router-dom";

const Top = () => {
  return (
    <div>
      <div className="top">
        <img src={ria}></img>
        <h1></h1>
        <h2></h2>
        <p><Link to="/nameset"></Link></p>
        <p className="end"><Link to="/info"></Link></p>
      </div>
      <Footer/>
    </div>
  )
}
export default Top;