import React from "react";
import ria1 from '../img/result/kichiria.svg';
import ria2 from '../img/result/cyoukichiria.svg';
import ria3 from '../img/result/suekichiria.svg';
import ria4 from '../img/result/nemuria.svg';
import ria5 from '../img/result/cyuukichiria.svg';
import ria6 from '../img/result/ataria.svg';
import ria7 from '../img/result/oomoria.svg';
import ria8 from '../img/result/maturia.svg';
import ria9 from '../img/result/ryouria.svg';
import nfria from '../img/NotFound.svg';

const ResultImg = (props) => {
    //TODO : 
    const target = props.number
    if (target === 0) {
        return <img src={ria1} alt="ria" />
    }else if (target === 1){
        return <img src={ria2} alt="ria" />
    }else if (target === 2){
        return <img src={ria3} alt="ria" />
    }else if (target === 3){
        return <img src={ria4} alt="ria" />
    }else if (target === 4){
        return <img src={ria5} alt="ria" />
    }else if (target === 5){
        return <img src={ria6} alt="ria" />
    }else if (target === 6){
        return <img src={ria7} alt="ria" />
    }else if (target === 7){
        return <img src={ria8} alt="ria" />
    }else if (target === 8){
        return <img src={ria9} alt="ria" />
    }else {
        return <img src={nfria} alt="ria" />
    }
}
export default ResultImg;